//  frontend/pages/index.js
import { useEffect, useState } from "react";
import Head from "next/head";
import ModelViewer from "../components/ModelViewer";

export default function Home() {
  const [pythonModelInfo, setPythonModelInfo] = useState(null);
  const [nextModelInfo, setNextModelInfo] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [pythonError, setPythonError] = useState(null);
  const [nextError, setNextError] = useState(null);

  useEffect(() => {
    // Fetch data from both backends
    const fetchData = async () => {
      // Fetch from Python backend
      try {
        console.log("Fetching from Python backend...");
        const pythonResponse = await fetch(
          "http://localhost:8000/python-model-info",
          {
            method: "GET",
            headers: {
              Accept: "application/json",
            },
          }
        );

        console.log("Python backend response status:", pythonResponse.status);

        if (!pythonResponse.ok) {
          throw new Error(`HTTP error! Status: ${pythonResponse.status}`);
        }

        const pythonData = await pythonResponse.json();
        console.log("Python data received:", pythonData);
        setPythonModelInfo(pythonData);
      } catch (err) {
        console.error("Error fetching from Python backend:", err);
        setPythonError(`Failed to fetch from Python backend: ${err.message}`);
      }

      // Fetch from Next.js API route
      try {
        console.log("Fetching from Next.js API route...");
        const nextResponse = await fetch("/api/nextjs-model-info");

        console.log("Next.js API response status:", nextResponse.status);

        if (!nextResponse.ok) {
          throw new Error(`HTTP error! Status: ${nextResponse.status}`);
        }

        const nextData = await nextResponse.json();
        console.log("Next.js data received:", nextData);
        setNextModelInfo(nextData);
      } catch (err) {
        console.error("Error fetching from Next.js API:", err);
        setNextError(`Failed to fetch from Next.js API: ${err.message}`);
      }

      // Set loading to false regardless of success/failure
      setLoading(false);
    };

    fetchData();
  }, []);

  return (
    <div className="container">
      <Head>
        <title>3D Model Viewer</title>
        <meta
          name="description"
          content="3D Model Viewer with integrated backends"
        />
        <link rel="icon" href="/favicon.ico" />
      </Head>

      <main>
        <div className="viewer-container">
          <ModelViewer />
        </div>

        <div className="info-container">
          <div className="info-panel">
            <h2>Python Backend Info</h2>
            {loading ? (
              <p>Loading data from Python backend...</p>
            ) : pythonError ? (
              <p className="error">{pythonError}</p>
            ) : (
              <div>
                {pythonModelInfo ? (
                  <div className="model-info">
                    <p>
                      <strong>Model Name:</strong> {pythonModelInfo.model_name}
                    </p>
                    <p>
                      <strong>Vertex Count:</strong>{" "}
                      {pythonModelInfo.vertex_count}
                    </p>
                    <p>
                      <strong>Texture Details:</strong>{" "}
                      {pythonModelInfo.texture_details}
                    </p>
                  </div>
                ) : (
                  <p>No data received from Python backend</p>
                )}
              </div>
            )}
          </div>

          <div className="info-panel">
            <h2>Next.js Backend Info</h2>
            {loading ? (
              <p>Loading data from Next.js API...</p>
            ) : nextError ? (
              <p className="error">{nextError}</p>
            ) : (
              <div>
                {nextModelInfo ? (
                  <div className="model-info">
                    <p>
                      <strong>Model Scale:</strong> {nextModelInfo.scale}
                    </p>
                    <p>
                      <strong>Face Count:</strong> {nextModelInfo.face_count}
                    </p>
                    <p>
                      <strong>Created By:</strong> {nextModelInfo.created_by}
                    </p>
                  </div>
                ) : (
                  <p>No data received from Next.js API</p>
                )}
              </div>
            )}
          </div>
        </div>
      </main>

      <style jsx>{`
.container {
    min-height: 100vh;
    padding: 0 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

main {
    width: 100%;
    max-width: 1200px;
    padding: 2rem 0;
    display: flex;
    flex-direction: column; /* Stack elements vertically */
    align-items: center;
    gap: 20px;
}

.title {
    margin: 0 0 2rem;
    line-height: 1.15;
    font-size: 2.5rem;
    text-align: center;
    color: #0070f3;
}

    .viewer-container {
  width: 100%; /* Full width for better visibility */
  height: 70vh; /* Takes majority of the screen */
  border: 1px solid #eaeaea;
  border-radius: 10px;
  overflow: hidden;
}

.info-container {
  width: 80%; /* Less space for info */
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  gap: 20px;
}

.info-panel {
  flex: 1;
  padding: 1.5rem;
  border: 1px solid #eaeaea;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #f9f9f9;
}      
.info-panel:hover {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}

@media (max-width: 768px) {
    .info-container {
        flex-direction: column; /* Stack info below 3D model on small screens */
        width: 100%;
    }
}

.viewer-container,
.info-container {
      width: 100%;
   }
}

.info-panel h2 {
    margin-top: 0;
    color: #333;
    border-bottom: 1px solid #eaeaea;
    padding-bottom: 0.5rem;
}

.model-info p {
    margin: 0.7rem 0;
}

.error {
    color: #e53e3e;
    font-weight: 500;
    padding: 0.5rem;
    background-color: #fed7d7;
    border-radius: 5px;
}
      `}</style>
    </div>
  );
}
